  SendTransactionEmail = (pOrderID) => {
    const urlString = 'https://newyoumedspaandaesthetics.com/email.php'
    const currentDate = this.GetCurrentDate('/');

    var details = {
      fname: this.state.txtFullName,
      date: currentDate, // "27/03/2020 06:50 PM",
      email: this.state.txtEmailAddress,
      invoice_no: this.state.txtInvoiceNo,
      order_id: pOrderID,
      amount: '$' + this.state.txtAmount,
      reason: this.state.txtReason
    };
    let lData = JSON.stringify(details)
    fetch(urlString, {
      "method": "POST",
      "headers": {
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8',
        "accept": "application/json"
      },
      "body": lData
    })
      .then(response => response.text())
      .then(response => {
      })
      .catch(err => {
        console.log(err);
      });

  }

  GetCurrentDate = (separator = '') => {

    let newDate = new Date()
    let date = newDate.getDate();
    let month = newDate.getMonth() + 1;
    let year = newDate.getFullYear();
    let hours = newDate.getHours();
    let minutes = newDate.getMinutes();

    return `${month < 10 ? `0${month}` : `${month}`}${separator}${date}${separator}${year} ${hours}` + `:` + `${minutes}`
  }

