import React from "react";
import ReactDOM from "react-dom";
import scriptLoader from "react-async-script-loader";
import Spinner from "./Spinner";

const CLIENT = {
  sandbox: "your sandbox id",
  production: "your client id"
};

const CLIENT_ID = 'AX6syvFZITXH1yKbHn36BEzGmzMOOXRJFggcQQSbHbNLgJYzYAFEhFPFhLNrGyqXHqNXAZbiA5-pACgE'
//process.env.NODE_ENV === "production" ? CLIENT.production : CLIENT.sandbox;

let PayPalButton = null;
class PaypalButton extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showButtons: false,
      loading: true,
      paid: false
    };

    window.React = React;
    window.ReactDOM = ReactDOM;
  }

  componentDidMount() {
    const { isScriptLoaded, isScriptLoadSucceed } = this.props;

    if (isScriptLoaded && isScriptLoadSucceed) {
      PayPalButton = window.paypal.Buttons.driver("react", { React, ReactDOM });
      this.setState({ loading: false, showButtons: true });
    }
  }

  componentWillReceiveProps(nextProps) {
    const { isScriptLoaded, isScriptLoadSucceed } = nextProps;

    const scriptJustLoaded =
      !this.state.showButtons && !this.props.isScriptLoaded && isScriptLoaded;

    if (scriptJustLoaded) {
      if (isScriptLoadSucceed) {
        PayPalButton = window.paypal.Buttons.driver("react", {
          React,
          ReactDOM
        });
        this.setState({ loading: false, showButtons: true });
      }
    }
  }
  createOrder = (data, actions) => {
    return actions.order.create({
      purchase_units: [
        {
          description: this.props.invoice, //this.props.selectedSongID, //"Body slim payment",
          invoice_id: this.props.invoice, //'0069872255',
          custom_id: this.props.orderReason, //'Body slim payment',
          amount: {
            currency_code: "USD",
            value: this.props.orderAmount//10
          }
        }
      ],
      application_context: {
        shipping_preference: 'NO_SHIPPING'
      }
    });
  };

  onApprove = (data, actions) => {
    console.log("onApprove: ", data);
    actions.order.capture().then(details => {
      const paymentData = {
        payerID: data.payerID,
        orderID: data.orderID
      };
      console.log("Payment data: ", data);
      console.log("Payment details: ", details);
      console.log("Payment Approved: ", paymentData);
      this.setState({ showButtons: false, paid: true });
    });

    this.props.PaymentResponse(data.orderID);
  };
  onShippingChange = (data, actions) => {
    //if not needed do nothing..
    return actions.resolve();
  }

  render() {
    const { showButtons, loading, paid } = this.state;

    return (
      <div className="main">
        {loading && <Spinner />}

        {showButtons && (
          <div>
            <div>
              <h2>.</h2>
            </div>

            <PayPalButton
              createOrder={(data, actions) => this.createOrder(data, actions)}
              onApprove={(data, actions) => this.onApprove(data, actions)}
              onShippingChange={(data, actions) => this.onShippingChange(data, actions)}
            />
          </div>
        )}

        {paid && (
          <div className="main">
            <h2>
              Congrats! you just paid successfully. An invoice has been sent to your email address.
            </h2>
          </div>
        )}
      </div>
    );
  }
}

export default scriptLoader(`https://www.paypal.com/sdk/js?client-id=${CLIENT_ID}&currency=USD&buyer-country=US`)(PaypalButton);